*{
  margin: 0;
  padding: 0;
  list-style: none;
  outline: none;
  box-sizing: border-box;
}
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, system-ui, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
.todo{
  position: absolute;
  left: 50%;
  top: 50%;
  display: flex;
  font-size: 14px;
  box-shadow: 1px 2px 20px #ececec;
  border-radius: 8px;
  border: 1px solid #f1f1f1;
  width: 750px;
  min-height: 530px;
  transform: translate(-50%,-50%);
  &__sidebar{
    background-color: #f4f6f8;
    width:200px;
    height: 100%;
    border-right: 1px solid #f1f1f1;
    padding: 60px 20px;
  }
  
}

.todo {
  position: absolute;
  left: 50%;
  top: 50%;
  font-size: 14px;
  box-shadow: 1px 2px 20px #ececec;
  border-radius: 8px;
  border: 1px solid #f1f1f1;
  width: 750px;
  height: 530px;
  transform: translate(-50%,-50%);

  &__sidebar {
    background-color: #f4f6f8;
    width:200px;
    height: 100%;
    border-right: 1px solid #f1f1f1;
    padding: 60px 20px;
  }

  &__tasks {
    flex: 1;
    padding: 60px;
    overflow: auto;
  }
}

.button {
  background: #4dd599;
  border-radius: 4px;
  color: #fff;
  border: 0;
  padding: 10px 20px;
  cursor: pointer;
  font-size: 14px;

  &:hover {
    background: darken(#4dd599, 6%);
  }

  &:active {
    background: darken(#4dd599, 10%);
  }

  &:disabled {
    background: #d8d8d8;
  }

  &--grey {
    background: #f4f6f8;
    color: #444444;

    &:hover {
      background: darken(#f4f6f8, 3%);
    }

    &:active {
      background: darken(#f4f6f8, 6%);
    }
  }
}

.field {
  background: #ffffff;
  border: 1px solid #efefef;
  box-sizing: border-box;
  border-radius: 4px;
  padding: 8px 12px;
  width: 100%;
  font-size: 14px;

  &:focus {
    border-color: #dbdbdb;
  }
}
